import {
  imageShowUrl,
  pdfCdnUrl,
} from "../../../../Services/UrlConfig/BaseUrl";
import {
  assestsFeedUrl,
  assestsUserFinanceUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../../InstituteMember/InstituteFeedback/InstituteFeedback.module.css";
import { useTranslation } from "react-i18next";
import CreateButton from "../../../../Ui/Button/CreateButton";
import { useState } from "react";
import FinanceOtherFeesCollectInner from "../FinanceOtherFeesCollect/FinanceOtherFeesCollectInner";
import FinanceOtherCollectLogsFeesDetailCardMenu from "./FinanceOtherCollectLogsFeesDetailCardMenu";
import NotifyEvent from "../../../../Utils/Snackbar/NotifyEvent";
const FinanceOtherCollectLogsFeesDetailCard = ({
  student,
  financeId,
  onRefetch,
  otherFeesId,
  instituteId,
  ofid,
  isCashierAccess,
  collectStaffId,
  cardId,
  cardAmount,
}) => {
  const { t } = useTranslation();
  const [step, setStep] = useState("");
  const [snackbar, setSnackbar] = useState({
    open: false,
    msg: "",
    variant: "error",
  });

  const onCollect = () => {
    if (isCashierAccess === "ALLOW") {
      setStep(1);
    } else {
      setSnackbar({
        msg: "You are not Cashier Manager",
        open: true,
        variant: "error",
      });
    }
  };

  const onCloseStep = () => {
    setStep("");
  };
  const onToggle = (e) => {
    setStep(2);
  };
  return (
    <>
      <div className={style.iadcc_card_hover}>
        <div className={style.sf_card_contatiner}>
          <img
            src={
              student?.studentProfilePhoto
                ? `${imageShowUrl}/${student.studentProfilePhoto}`
                : `${assestsFeedUrl}/feed-user-avatar.svg`
            }
            loading="lazy"
            alt="avatar"
            style={{
              width: "2.5rem",
              height: "2.5rem",
            }}
          />

          <div className={style.sf_card_contatiner_inner}>
            <h6>
              {`${student?.studentFirstName ?? ""} ${
                student?.studentMiddleName ?? ""
              } ${student?.studentLastName ?? ""}`}
            </h6>
            <p>
              {t("gr_number")} {": "}
              {student?.studentGRNO ?? ""}
            </p>
          </div>
        </div>
        {student?.other_fees_obj?.status === "Paid" ? (
          <div
            style={{
              display: "flex",
              alignItems: "flex-start",
              gap: "1rem",
            }}
          >
            <div className={style.sf_card_contatiner_inner}>
              <h6>Rs. {cardAmount ?? 0}</h6>
              <p>{t("amount")}</p>
            </div>
            <div className={style.sf_card_contatiner_inner}>
              {student?.other_fees_obj?.receipt_file ? (
                <a
                  href={`${pdfCdnUrl}/${student?.other_fees_obj?.receipt_file}`}
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <img
                    src={`${assestsUserFinanceUrl}/receipt.svg`}
                    alt="receipt icon"
                    style={{
                      height: "1.5rem",
                    }}
                  />
                </a>
              ) : null}
            </div>
            <img
              src={`${assestsUserFinanceUrl}/menu.svg`}
              alt="menu icon"
              onClick={() => setStep(3)}
            />
          </div>
        ) : (
          <div
            style={{
              display: "flex",
              alignItems: "flex-start",
              gap: "1rem",
            }}
          >
            <div className={style.sf_card_contatiner_inner}>
              <h6>Rs. {cardAmount ?? 0}</h6>
              <p>{t("amount")}</p>
            </div>
            <CreateButton
              label="collect_fees"
              onAction={onCollect}
              customStyle={{
                margin: "0",
                padding: "0.4rem 1rem",
              }}
            />
            <img
              src={`${assestsUserFinanceUrl}/menu.svg`}
              alt="menu icon"
              onClick={onToggle}
            />
          </div>
        )}
      </div>
      {step === 1 ? (
        <FinanceOtherFeesCollectInner
          financeId={financeId}
          onClose={onCloseStep}
          onRefetch={onRefetch}
          student={student}
          otherFeesId={otherFeesId}
          instituteId={instituteId}
          collectStaffId={collectStaffId}
          cardId={cardId}
          cardAmount={cardAmount}
        />
      ) : step === 2 ? (
        <FinanceOtherCollectLogsFeesDetailCardMenu
          onClose={onCloseStep}
          onRefetch={onRefetch}
          menuData={student}
          ofid={ofid}
        />
      ) : step === 3 ? (
        <FinanceOtherCollectLogsFeesDetailCardMenu
          onClose={onCloseStep}
          onRefetch={onRefetch}
          menuData={student}
          ofid={ofid}
          feeReceiptId={student?.other_fees_obj?.fee_receipt}
          isFee
        />
      ) : null}
      <NotifyEvent eventState={snackbar} eventStateHandler={setSnackbar} />
    </>
  );
};

export default FinanceOtherCollectLogsFeesDetailCard;
