import UserMemberContainerWrapper from "../../../../Ui/UserMember/UserMemberContainerWrapper";
import style from "../../FinanceManager.module.css";
import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import HorizontalTabWrapper from "../../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import HorizontalTabCard from "../../../../Ui/Tab/Horizontal/HorizontalTabCard";
import CreateButton from "../../../../Ui/Button/CreateButton";
import { useCallback, useState } from "react";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import {
  useFinanceOneOtherFeesStudentList,
  useFinanceOtherFeeAddNonExistStudentCollect,
  useFinanceOtherFeeAddSingleStudentCollect,
  useFinanceOtherFeeAddStudent,
  // useFinanceOtherFeesCombinedStudentListExport,
  useFinanceOtherOneFeesTransaction,
} from "../../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import FinanceOtherFeesDetailTransaction from "./FinanceOtherFeesDetailTransaction";
import OtherFeesAddNonExistingStudent from "./OtherFeesAddNonExistingStudent";
import FinanceOtherFeesCollectWhenAdd from "../FinanceOtherFeesCollect/FinanceOtherFeesCollectWhenAdd";
import InstituteStudent from "../../../InstituteStudent/InstituteStudent";
import AfterCollectMiscellaneousFeesViewReceipt from "../FinanceOtherFeesCollect/AfterCollectMiscellaneousFeesViewReceipt";
import { useTranslation } from "react-i18next";
import NotifyEvent from "../../../../Utils/Snackbar/NotifyEvent";
import FinanceOtherCollectLogsFeesDetail from "./FinanceOtherCollectLogsFeesDetail";
import { assestsUtilityUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import FinanceOtherFeesDetailCombinedExport from "./FinanceOtherFeesDetailCombinedExport";
const FinanceOtherFeesDetail = ({ fid, instituteId, cashAuthority }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const [disabled, setDisabled] = useState(false);
  const [step, setStep] = useState("");
  const [singleStudent, setSingleStudent] = useState(null);
  const [studentId, setStudentId] = useState([]);

  const [snackbar, setSnackbar] = useState({
    open: false,
    msg: "",
    variant: "error",
  });

  // const [financeOtherFeesCombinedStudentListExpor] =
  //   useFinanceOtherFeesCombinedStudentListExport();

  const [financeOtherFeeAddNonExistStudentCollect] =
    useFinanceOtherFeeAddNonExistStudentCollect();
  const [financeOtherFeeAddStudent] = useFinanceOtherFeeAddStudent();
  const [financeOtherFeeAddSingleStudentCollect] =
    useFinanceOtherFeeAddSingleStudentCollect();

  const {
    financeOneOtherFeesStudentList,
    financeOneOtherFeesStudentListRefetch,
  } = useFinanceOneOtherFeesStudentList({
    data: {
      ofid: getQuery.state?.feesId,
      page: 1,
      limit: 10,
      search: "",
      type: "",
    },
    skip: !getQuery.state?.feesId,
  });

  const {
    financeOtherOneFeesTransaction,
    financeOtherOneFeesTransactionRefetch,
  } = useFinanceOtherOneFeesTransaction({
    data: {
      uid: instituteId,
      page: 1,
      limit: 10,
      search: "",
      filter: "",
      ofid: getQuery.state?.feesId,
    },
    skip: instituteId ? !getQuery.state?.feesId : !instituteId,
  });

  // const onExportList = () => {
  //   if (getQuery.state?.feesId) {
  //     setDisabled((pre) => !pre);
  //     financeOtherFeesCombinedStudentListExpor({
  //       ofid: getQuery.state?.feesId,
  //     })
  //       .then(() => {
  //         let url =
  //           getQuery.state?.urlOffSet === "Institute"
  //             ? `/q/${params.username}/institute/member/finance?type=export`
  //             : `/q/${params.username}/member/staff/finance?type=export`;
  //         navigate(url, {
  //           state: getQuery.state,
  //         });
  //       })
  //       .catch({});
  //   }
  // };

  const onClose = () => {
    setStep("");
  };

  const onAddNonStudentToggle = () => {
    if (cashAuthority?.includes(getQuery?.state?.sid)) {
      setStep(1);
    } else {
      setSnackbar({
        msg: "You are not Cashier Manager",
        open: true,
        variant: "error",
      });
    }
  };
  const onEnterStudentData = (val) => {
    setSingleStudent(val);
    setStep(2);
  };

  const onAddNonExistingSingleStudent = (data) => {
    if (fid && singleStudent?.student_name && getQuery.state?.feesId) {
      setDisabled((pre) => !pre);
      financeOtherFeeAddNonExistStudentCollect({
        fid: fid,
        bodyParameter: {
          ...singleStudent,
          ofid: getQuery.state?.feesId,
          ...data,
          staff_id: getQuery?.state?.sid ?? "",
        },
      })
        .then(() => {
          setSingleStudent(null);
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  const onAddStudentToggle = () => {
    setStep(3);
  };
  const onAddSingleStudentToggle = () => {
    if (cashAuthority?.includes(getQuery?.state?.sid)) {
      setStep(4);
    } else {
      setSnackbar({
        msg: "You are not Cashier Manager",
        open: true,
        variant: "error",
      });
    }
  };

  const onSelectStudent = (val) => {
    if (studentId?.includes(val?._id)) {
      setStudentId((prev) => prev?.filter((dt) => dt !== val?._id));
    } else {
      setStudentId((prev) => [...prev, val?._id]);
    }
  };

  const onAddStudent = () => {
    if (fid && studentId?.length > 0 && getQuery.state?.feesId) {
      setDisabled((pre) => !pre);
      financeOtherFeeAddStudent({
        fid: fid,
        bodyParameter: {
          students: studentId,
          ofid: getQuery.state?.feesId,
        },
      })
        .then(() => {
          financeOneOtherFeesStudentListRefetch();
          setDisabled((pre) => !pre);
          onClose();
          setStudentId([]);
          setSingleStudent(null);
        })
        .catch({});
    }
  };

  const onSelectSingleStudent = (val) => {
    setSingleStudent(val);
    setStep(5);
  };

  const onAddSingleStudent = (data) => {
    if (fid && singleStudent?._id && getQuery.state?.feesId) {
      setDisabled((pre) => !pre);
      financeOtherFeeAddSingleStudentCollect({
        fid: fid,
        bodyParameter: {
          students: [singleStudent?._id],
          ofid: getQuery.state?.feesId,
          ...data,
          staff_id: getQuery?.state?.sid ?? "",
        },
      })
        .then((res) => {
          setStep(6);
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onExport = () => {
    setStep(7);
  };
  const onCloseReceipt = () => {
    setSingleStudent(null);
    onClose();
  };

  const onNavigate = useCallback(() => {
    let url = "";
    url =
      getQuery.state?.urlOffSet === "Institute"
        ? `/q/${params.username}/institute/member/finance?type=other-fees`
        : `/q/${params.username}/member/staff/finance?type=other-fees`;
    navigate(url, {
      state: getQuery.state,
    });
  }, [navigate, getQuery.state, params.username]);

  const onNavigateReceipt = useCallback(() => {
    let url = "";
    url =
      getQuery.state?.urlOffSet === "Institute"
        ? `/q/${params.username}/institute/member/finance?type=miscellaneous-receipt`
        : `/q/${params.username}/member/staff/finance?type=miscellaneous-receipt`;
    navigate(url, {
      state: getQuery.state,
    });
  }, [navigate, getQuery.state, params.username]);
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <div
              className={style.fm_header_start_container}
              style={{
                gap: "0.2rem",
              }}
            >
              <img
                alt="search icon"
                src={`${assestsUtilityUrl}/back.svg`}
                style={{
                  height: "2rem",
                  width: "2rem",
                }}
                onClick={onNavigate}
              />
              <h6>
                {getQuery.state?.feesName ?? ""}
                {` `}(
                <span
                  style={{
                    color: "#00f",
                  }}
                >
                  {getQuery.state?.otherData?.paid}
                </span>
                /{getQuery.state?.otherData?.rmaining})
              </h6>
            </div>
            <div className={style.fm_header_start_container}>
              <CreateButton
                label="new_receipt"
                onAction={onNavigateReceipt}
                customStyle={{
                  margin: "0",
                  padding: "0.3rem 1rem",
                }}
              />
              <CreateButton
                label="add_single_student"
                customStyle={{
                  margin: "0",
                  padding: "0.4rem 0.5rem",
                  width: "fit-content",
                }}
                onAction={onAddSingleStudentToggle}
              />

              <CreateButton
                label="add_student"
                customStyle={{
                  margin: "0",
                  padding: "0.4rem 0.5rem",
                  width: "fit-content",
                }}
                onAction={onAddStudentToggle}
              />
              <CreateButton
                label="add_non_existing_student"
                customStyle={{
                  margin: "0",
                  padding: "0.4rem 0.5rem",
                  width: "fit-content",
                }}
                onAction={onAddNonStudentToggle}
              />
              <CreateButton
                label="export"
                customStyle={{
                  margin: "0",
                  padding: "0.4rem 0.5rem",
                  width: "fit-content",
                }}
                onAction={onExport}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <HorizontalTabWrapper>
          {tabList.map((tab) => (
            <HorizontalTabCard
              key={tab?.id}
              tab={tab}
              isLink
              isActive={tab.groupIs?.includes(searchParams.get("n"))}
              parentState={getQuery.state}
              url={
                getQuery.state?.urlOffSet === "Institute"
                  ? `/q/${params.username}/institute/member/finance?type=other-fees-detail${tab.urlKey}`
                  : `/q/${params.username}/member/staff/finance?type=other-fees-detail${tab.urlKey}`
              }
            />
          ))}
        </HorizontalTabWrapper>
        <BorderBottom
          customStyleBorder={{
            marginTop: "0",
          }}
        />
        {searchParams.get("n") === "receipt" ? (
          <FinanceOtherCollectLogsFeesDetail
            ofid={getQuery.state?.feesId}
            instituteId={instituteId}
            financeId={fid}
            isCashierAccess={
              cashAuthority?.includes(getQuery?.state?.sid) ? "ALLOW" : "DENY"
            }
            collectStaffId={getQuery?.state?.sid}
          />
        ) : (
          <FinanceOtherFeesDetailTransaction
            ofid={getQuery.state?.feesId}
            instituteId={instituteId}
          />
        )}
      </UserMemberContainerWrapper>

      {disabled && <QvipleLoader />}

      {step === 1 ? (
        <OtherFeesAddNonExistingStudent
          onClose={onClose}
          singleStudent={singleStudent}
          onNext={onEnterStudentData}
        />
      ) : step === 2 ? (
        <FinanceOtherFeesCollectWhenAdd
          onClose={() => setStep(1)}
          onParentClose={() => {}}
          instituteId={instituteId}
          onGrabCollectFees={onAddNonExistingSingleStudent}
          singleStudent={singleStudent}
          isOtherFees="OTHER_FEES"
          isNonExisting
          applicant={{
            head: getQuery.state?.otherData?.payable_amount,
          }}
        />
      ) : step === 3 ? (
        <InstituteStudent
          title={t("select_student")}
          onClose={onClose}
          instituteId={instituteId}
          isMultiSelect
          onMultiSelect={onSelectStudent}
          onDone={onAddStudent}
          selectList={studentId}
          buttonTitle="add"
          isSearchOnly
        />
      ) : step === 4 ? (
        <InstituteStudent
          title={t("select_student")}
          onClose={onClose}
          instituteId={instituteId}
          onSelectStudent={onSelectSingleStudent}
          selectedId={singleStudent?._id}
          isSearchOnly
        />
      ) : step === 5 ? (
        <FinanceOtherFeesCollectWhenAdd
          onClose={onClose}
          onParentClose={() => {}}
          instituteId={instituteId}
          onGrabCollectFees={onAddSingleStudent}
          singleStudent={singleStudent}
          isOtherFees="OTHER_FEES"
          // isNonExisting
          applicant={{
            head: getQuery.state?.otherData?.payable_amount,
          }}
          ofid={getQuery.state?.feesId}
          financeId={fid}
        />
      ) : step === 6 ? (
        <AfterCollectMiscellaneousFeesViewReceipt
          applicant={singleStudent}
          onClose={onCloseReceipt}
          ofid={getQuery.state?.feesId}
        />
      ) : step === 7 ? (
        <FinanceOtherFeesDetailCombinedExport
          onClose={onClose}
          ofid={getQuery.state?.feesId}
        />
      ) : null}
      <NotifyEvent eventState={snackbar} eventStateHandler={setSnackbar} />
    </>
  );
};

export default FinanceOtherFeesDetail;

const tabList = [
  {
    label: "transaction",
    id: "transaction",
    urlKey: "",
    groupIs: ["", null],
  },
  {
    label: "receipt",
    id: "receipt",
    urlKey: "&n=receipt",
    groupIs: ["receipt"],
  },
  // {
  //   label: "transaction",
  //   id: "transaction",
  //   urlKey: "&n=transaction",
  //   groupIs: ["transaction"],
  // },
];

