import { useTranslation } from "react-i18next";
import ModalMenu from "../../../../Ui/Modal/ModalMenu";
import { useState } from "react";
import {
  useFinanceOtherFeeRemovePaidStudent,
  useFinanceOtherFeeRemoveStudent,
} from "../../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import NotifyEvent from "../../../../Utils/Snackbar/NotifyEvent";

import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import FormInput from "../../../../Ui/Input/FormInput";
import {
  existingRequiredField,
  requiredField,
} from "../../../../Validation/functions/checkingRequiredField";
import style from "../../Income/Income.module.css";
import CreateButton from "../../../../Ui/Button/CreateButton";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
const OtherFeesPaidStudentCardMenu = ({
  ofid,
  onClose,
  menuData,
  onRefetch,
  feeReceiptId,
  isFee,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [snackbar, setSnackbar] = useState({
    open: false,
    msg: "",
    variant: "error",
  });
  const [errorField, setErrorField] = useState("");
  const [state, setState] = useState({
    query: "",
  });
  const [isDelete, setIsDelete] = useState(false);

  const [financeOtherFeeRemoveStudent] = useFinanceOtherFeeRemoveStudent();
  const [financeOtherFeeRemovePaidStudent] =
    useFinanceOtherFeeRemovePaidStudent();

  const onDeleteFeeReceipt = () => {
    let validation = requiredField(state);
    if (existingRequiredField(validation)) {
      if (state.query === "Confirm" && feeReceiptId && ofid) {
        setIsDelete(false);
        setDisabled((pre) => !pre);
        financeOtherFeeRemovePaidStudent({
          fid: feeReceiptId,
          ofid: ofid,
        })
          .then((res) => {
            onRefetch();
            setDisabled((pre) => !pre);
            onClose();
          })
          .catch({});
      }
    } else {
      setErrorField(validation);
    }
  };
  const onRemove = () => {
    if (menuData?._id && ofid) {
      setDisabled((pre) => !pre);
      financeOtherFeeRemoveStudent({
        ofid: ofid,
        bodyParameter: {
          students: [menuData?._id],
        },
      })
        .then((res) => {
          setSnackbar({
            open: true,
            msg: "Time out of request...",
            variant: "error",
          });
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  return (
    <>
      {isDelete ? (
        <Modal onClose={() => setIsDelete(false)}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>{t("are_your_sure_to_delete_fee_receipt")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={() => setIsDelete(false)}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <FormInput
                label={t("type_confirm_to_continue")}
                placeholder={t("type_confirm_to_continue")}
                name="query"
                value={state.query}
                type="text"
                onChange={onInputChange}
                errorShow={errorField["query"]}
              />
              <CreateButton label="confirm" onAction={onDeleteFeeReceipt} />
            </UserMemberContentWrapper>
          </div>
        </Modal>
      ) : (
        <ModalMenu onClose={onClose} disabled={disabled}>
          {isFee ? (
            <h6 onClick={() => setIsDelete(true)}>{t("delete")}</h6>
          ) : (
            <h6 onClick={onRemove}>{t("remove")}</h6>
          )}
        </ModalMenu>
      )}

      <NotifyEvent eventState={snackbar} eventStateHandler={setSnackbar} />
    </>
  );
};

export default OtherFeesPaidStudentCardMenu;
