import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { useFinanceOtherOneFeesTransaction } from "../../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import style from "../../FinanceManager.module.css";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import FinanceOtherFeesDetailTransactionCard from "./FinanceOtherFeesDetailTransactionCard";
const FinanceOtherFeesDetailTransaction = ({ instituteId, ofid }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");

  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const {
    financeOtherOneFeesTransaction,
    financeOtherOneFeesTransactionLoading,
    financeOtherOneFeesTransactionRefetch,
  } = useFinanceOtherOneFeesTransaction({
    data: {
      uid: instituteId,
      page: page,
      limit: 10,
      search: search,
      filter: "",
      ofid: ofid,
    },
    skip: instituteId ? !ofid : !instituteId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (financeOtherOneFeesTransaction?.list?.length) {
      let obj = nextPage(financeOtherOneFeesTransaction?.list?.length);
      setState(() => obj);
    }
  }, [financeOtherOneFeesTransaction?.list?.length]);
  const onSearchEvent = (e) => {
    if (
      !financeOtherOneFeesTransactionLoading &&
      financeOtherOneFeesTransaction?.list?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };

  const onRefetch = () => {
    setPage((prev) => (prev = 1));
    financeOtherOneFeesTransactionRefetch();
  };

  return (
    <>
      <UserMemberContentWrapper>
        <div className={style.fm_search_full}>
          <section className={style.fm_search_full_container}>
            <div className={style.fm_search_full_container_inner}>
              <img
                className={style.fm_search_full_icon}
                alt="search icon"
                src={`${assestsNavbarUrl}/navbar-search.svg`}
              />
              <input
                type="text"
                placeholder={t("search")}
                onChange={onSearchEvent}
                onKeyUp={onCallBindSearch}
                value={searchCopy}
              />
            </div>
          </section>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0",
        }}
      >
        {financeOtherOneFeesTransaction?.list?.map((history, index) =>
          financeOtherOneFeesTransaction?.list?.length === index + 1 ? (
            <div key={history?._id} ref={ref}>
              <FinanceOtherFeesDetailTransactionCard
                history={history}
                onRefetch={onRefetch}
                ofid={ofid}
              />
            </div>
          ) : (
            <FinanceOtherFeesDetailTransactionCard
              history={history}
              key={history?._id}
              onRefetch={onRefetch}
              ofid={ofid}
            />
          )
        )}

        {financeOtherOneFeesTransactionLoading ? (
          search ? (
            <QvipleLoader />
          ) : (
            <Loading />
          )
        ) : search ? (
          financeOtherOneFeesTransaction?.list?.length === 0 && (
            <EmptyMessage
              title="No receipts found related this search."
              customStyleContainer={{
                marginTop: "1.5rem",
              }}
            />
          )
        ) : (
          financeOtherOneFeesTransaction?.list?.length === 0 && (
            <EmptyMessage
              title="No receipts added."
              customStyleContainer={{
                marginTop: "1.5rem",
              }}
            />
          )
        )}
      </UserMemberContentWrapper>
    </>
  );
};

export default FinanceOtherFeesDetailTransaction;
