import { useState } from "react";
import style from "../../Income/Income.module.css";
import { useTranslation } from "react-i18next";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import FormCalendarInput from "../../../../Ui/Input/FormCalendarInput";
import CustomSelect from "../../../../Ui/Select/CustomSelect";
import {
  slashDateFormat,
  ymdSlashDateFormat,
} from "../../../../Utils/Functions/dateUtility";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import CreateButton from "../../../../Ui/Button/CreateButton";
import { useMiscellanesouDublicateCombinedExport } from "../../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import {
  existingRequiredField,
  requiredField,
} from "../../../../Validation/functions/checkingRequiredField";
import { useLocation, useNavigate, useParams } from "react-router-dom";
// import TileWrapper from "../../../../Ui/Tab/Tile/TileWrapper";
// import TileCard from "../../../../Ui/Tab/Tile/TileCard";
// import CashierDorpdown from "../../FinanceDaybook/CashierDorpdown";

const FinanceOtherFeesDetailCombinedExport = ({
  onClose,
  // fid,
  // instituteId,
  ofid,
}) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();
  const [state, setState] = useState({
    from: "",
    to: "",
    payment_type: "",
    // staff: "",
    // cashier_type: "",
  });
  const [disabled, setDisabled] = useState(false);
  const [errorField, setErrorField] = useState("");
  const [miscellanesouDublicateCombinedExport] =
    useMiscellanesouDublicateCombinedExport();

  const onTransactionDate = (val, flow) => {
    setState((prev) => ({
      ...prev,
      [flow]: ymdSlashDateFormat(val),
    }));
  };
  const onSelectType = (val) => {
    setState((prev) => ({
      ...prev,
      payment_type: val,
    }));
  };
  // const onSelectCashier = (val) => {
  //   setState((prev) => ({
  //     ...prev,
  //     staff: val,
  //   }));
  // };

  // const onCashierType = (val) => {
  //   if (val === "ALL") {
  //     setState((prev) => ({
  //       ...prev,
  //       cashier_type: val,
  //       staff: "",
  //     }));
  //   } else {
  //     setState((prev) => ({
  //       ...prev,
  //       cashier_type: val,
  //       staff: "",
  //     }));
  //   }
  // };
  const onFilter = () => {
    let validation = requiredField(state, []);
    // let validation = requiredField(state, ["staff", "cashier_type"]);
    if (existingRequiredField(validation) && ofid) {
      setDisabled((pre) => !pre);
      miscellanesouDublicateCombinedExport({
        ofid: ofid,
        bodyParameter: {
          // bank: bankId,
          from: state.from,
          to: state.to,
          payment_type: state.payment_type,
          // staff: state.staff?._id ?? "",
        },
      })
        .then(() => {
          setDisabled((pre) => !pre);
          let url =
            getQuery.state?.urlOffSet === "Institute"
              ? `/q/${params.username}/institute/member/finance?type=export`
              : `/q/${params.username}/member/staff/finance?type=export`;
          navigate(url, {
            state: getQuery.state,
          });
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };
  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("export")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <FormCalendarInput
              label={t("from")}
              placeholder={t("please_select_from_date")}
              name={"from"}
              value={state.from ? slashDateFormat(state.from) : ""}
              onChange={(e) => onTransactionDate(e, "from")}
              errorShow={errorField["from"]}
            />
            <FormCalendarInput
              label={t("to_only")}
              placeholder={t("please_select_to_date")}
              name={"to"}
              value={state.to ? slashDateFormat(state.to) : ""}
              onChange={(e) => onTransactionDate(e, "to")}
              errorShow={errorField["to"]}
            />
            <CustomSelect
              selectLabel={t("payment_type")}
              selectedValue={t("please_select_payment_type")}
              options={["Payment Gateway / Online", "By Cash", "Total"]}
              onClick={onSelectType}
              defalutValue={state.payment_type}
              errorShow={errorField["payment_type"]}
            />
            {/* <TileWrapper label="cashier_type">
              {cashierLabel?.map((tile) => (
                <TileCard
                  key={tile?.key}
                  tile={tile}
                  onTile={onCashierType}
                  activeTile={state.cashier_type}
                />
              ))}
            </TileWrapper>
            {state.cashier_type === "PARTICULAR" ? (
              <CashierDorpdown
                onSelect={onSelectCashier}
                instituteId={instituteId}
                defalutValue={
                  state?.staff
                    ? `${state?.staff?.staffFirstName ?? ""} ${
                        state?.staff?.staffMiddleName ?? ""
                      } ${state?.staff?.staffLastName ?? ""}`
                    : ""
                }
              />
            ) : null} */}

            <CreateButton label="export" onAction={onFilter} />
          </UserMemberContentWrapper>
          {disabled && <QvipleLoader />}
        </div>
      </Modal>
    </>
  );
};

export default FinanceOtherFeesDetailCombinedExport;
export const cashierLabel = [
  {
    key: "ALL",
    name: "all",
  },
  {
    key: "PARTICULAR",
    name: "particular_cashier",
  },
];
